import React,{Component} from 'react'
import {connect} from 'react-redux'

import './styles.css'


class SuccessWindow extends Component {
    closeSuccessWindow(){

        this.props.onCloseSuccessWindow();

    }

    correctContent(data){

       if (data.success.bool){

          return(

              <div>

                  <p> {data.success.text} </p>

                  <button onClick={this.closeSuccessWindow.bind(this)}> {data.success.buttonValue} </button>


              </div>

          )

       }

       if (data.errror.bool) {

           return (

               <div>

                   <p>{data.errror.text}</p>

                   <button onClick= {this.closeSuccessWindow.bind(this)}>{data.errror.buttonValue} </button>


               </div>

           )

       }

       if (data.successCont.bool){

           return (

               <div>

                   <p> {data.successCont.text} </p>

                   <button onClick={this.closeSuccessWindow.bind(this)}> {data.successCont.buttonValue} </button>


               </div>

           )

       }

    }


    render(){

        return (

            <div className={this.props.name + " success"}>

                {this.correctContent(this.props.data)}


            </div>

        )
    }

}

export default connect(
    state=>({

    }),
    dispatch=>({

        onCloseSuccessWindow: ()=>{

            dispatch({type:"OPEN_SUCCESS_WINDOW"})

        }

    })
)(SuccessWindow)


