import React,{Component} from 'react'
import {connect} from 'react-redux'

import Header from '../../containers/Header/index'
import LazyImage from '../../components/LazyImage/index'
import images from '../../images'
import './styles.css'

 class About extends Component {

    render(){

        return (
            <div className="about">
                <Header/>
                <h1>О НАС</h1>
                <section>
                    <div className="wrapper">

                        <div id="column-1">

                            <h3  className="inline">{this.props.firstColumnText.p1.boldText}</h3>

                            <p  className="inline">
                                {this.props.firstColumnText.p1.text}
                            </p>

                            <div className="img_wrapper">
                                <LazyImage src={images[this.props.firstColumnText.image]} unloadSrc="spiner.svg" alt=""/>
                            </div>

                            <p>{this.props.firstColumnText.p2.text}</p>


                        </div>


                        <div id="column-2">

                            <div className="img_wrapper">

                                <LazyImage src={images[this.props.secondColumnText.image]} unloadSrc="spiner.svg" alt=""/>

                            </div>

                            <h2>{this.props.secondColumnText.p1.h1}</h2>

                            <ul>
                                {
                                    this.props.secondColumnText.p1.ul.map( (li,index) => {

                                        return <li key={index}>{li}</li>

                                    })
                                }
                            </ul>
                            <p>
                                {this.props.secondColumnText.p1.text}
                            </p>

                        </div>


                        <div id="column-3">
                            <h3 className="inline">{this.props.thirdColumnText.p1.boldText}</h3>
                            <p  className="inline">
                                {this.props.thirdColumnText.p1.text}
                            </p>

                            <p>{this.props.thirdColumnText.p2.text}</p>

                            <div className="img_wrapper">

                                <LazyImage unloadSrc="spiner.svg" src={images[this.props.thirdColumnText.image]} alt=""/>

                            </div>

                            <p>{this.props.thirdColumnText.p3.text}</p>



                        </div>

                    </div>
                </section>
            </div>
        )
    }

}

export default connect(
    state=>({
        firstColumnText: state.about.columns.first,
        secondColumnText: state.about.columns.second,
        thirdColumnText: state.about.columns.third
    })
)(About)



