import React,{Component} from 'react'
import {connect} from 'react-redux'

import  injectTapEventPlugin from  "react-tap-event-plugin"

injectTapEventPlugin();

import './styles.css'


class SideBar extends Component {

    changeUlClassName(){

        let typeList = document.getElementById('typeList');

        this.props.onChangeUlClassName(typeList.className);

    }
    changeActiveItem(e){

        this.props.onChangeActiveItem(e.target.id);

    }

    render(){

        return (

            <div className="type_bar">
                <h4>{this.props.h4}</h4>
                <p id={this.props.activeItem.id}
                   onMouseOver={this.changeUlClassName.bind(this)}
                   onMouseOut={this.changeUlClassName.bind(this)}
                   onTouchStart={this.changeUlClassName.bind(this)}
                   onBlur={this.changeUlClassName.bind(this)}

                >{this.props.activeItem.name}</p>

                <ul id="typeList" className={this.props.classTypesList}>

                    {
                        this.props.items.map((item,index)=>{

                            return <li onClick={this.changeActiveItem.bind(this)} key={index} id={item.id}>{item.name}</li>

                        })
                    }

                </ul>

            </div>
        )

    }

}

export default connect(
    state=>({
        h4:state.sidebar.categories.p,
        items:state.sidebar.categories.items,
        activeItem:state.sidebar.categories.activeItem,
        classTypesList:state.sidebar.classes.classTypesList

    }),
    dispatch=>({

        onChangeUlClassName: selectorClassName =>{

            dispatch({type:"SHOW_TYPE_LIST_ASIDE", payload:selectorClassName})

        },
        onChangeActiveItem: (targetId) =>{

            dispatch({type:"CHANGE_ACTIVE_ITEM", payload:targetId})

        }
    })
)(SideBar)


