import React, {Component} from 'react'
import {connect} from 'react-redux'
import {Route} from 'react-router'

import Products from "../../pages/Products";
import PartnersAndClients from "../../pages/PartnersAndClients"
import TradeMarks from "../../pages/TradeMarks"
import About from "../../pages/About"
import Contacts from "../../pages/Contacts"


 class App extends Component{


    render(){
        return (
            <div>
                <Route exact path={this.props.name} component={Products}/>
                <Route exact path={this.props.name + "partners_and_clients"} component={PartnersAndClients}/>
                <Route exact path={this.props.name + "trade_marks"} component={TradeMarks}/>
                <Route exact path={this.props.name + "about"} component={About}/>
                <Route exact path={this.props.name + "contacts"} component={Contacts}/>
            </div>
        )
    }

}

export default App;


