import React,{Component} from 'react'
import {connect} from 'react-redux'



import './styles.css'

class ContactsForm extends Component {


    ajax(e){



        const form = new FormData(document.getElementById('conatactForm'));

        fetch ( "buy.php", {

            method: "POST",
            body: form

        }).then( ( response ) => {

            if(response.status === 200){

                this.props.onCloseModalWindow('', {status: response.status});

                this.name.value = "";
                this.email.value = "";
                this.message.value = "";
                this.phone.value = ""


            }else{

                this.props.onCloseModalWindow('', response.status)

            }

        })
        e.preventDefault()

    }

    render(){

        return (
            <div className="content_form">
                <div className="header_form">
                    <h1 className="connect_with">Свяжитесь с нами</h1>
                    <p>Мы здесь, чтобы ответить на любые вопросы, которые могут возникнуть в отношении нашей компании. Обратитесь к нам, и мы в скором времени вам ответим.</p>
                </div>
                <form id="conatactForm" onSubmit={this.ajax.bind(this)}>
                    <div className="input_up">
                        <span className='name'><input ref={input=> this.name = input} type="text" required/></span>
                        <span className="email"><input ref={input=> this.email = input}  type="email" required/></span>
                    </div>
                    <span className="message" ><textarea ref={input=> this.message = input}></textarea></span><br/>
                    <span className="number"><input ref={input=> this.phone = input} type="phone" required/></span><br/>
                    <button type="submit">Отправить</button>

                </form>

            </div>
        )
    }

}

export default connect(
    state =>({}),
    dispatch=>({

        onCloseModalWindow: (emptyString, responseStatus)=>{

            dispatch({type:"MODAL_WINDOW", payload:emptyString})


            dispatch({type:"OPEN_SUCCESS_WINDOW", payload: responseStatus})


        }
    })

)(ContactsForm)




