import React,{Component} from 'react'
import {connect} from 'react-redux'

import Header from '../../containers/Header/index'
import images from '../../images'
import './styles.css'

class TradeMarks extends Component {

    render(){

        return (
            <div className='trade_marks'>
                <Header/>
                <h1 className="">ТОРГОВЫЕ МАРКИ</h1>
                <section >

                    <div className="wrapper">
                        <div id="column-1">
                            <div>
                                <img src={images["russian_flag.png"]} alt=""/>
                            </div>
                            <p>
                                {this.props.firstColumnText.p1}
                            </p>
                            <p>
                                {this.props.firstColumnText.p2}
                            </p>
                        </div>
                        <div id="column-2">
                            <div>
                                <img src={images["br_flag.png"]} alt=""/>
                            </div>
                            <p>
                                {this.props.secondColumnText.p1}
                            </p>
                            <p>
                                {this.props.secondColumnText.p2}
                            </p>
                            <p>
                                {this.props.secondColumnText.p3}
                            </p>

                        </div>

                        <div id="column-3">
                            <div>
                                <img src={images["swiss.png"]} alt=""/>
                            </div>
                            <p>{this.props.thirdColumnText.p1}</p>
                            <p>{this.props.thirdColumnText.p2}</p>
                            <p>{this.props.thirdColumnText.p3}</p>

                        </div>
                    </div>
                </section>
            </div>
        )
    }

}

export default connect(
    state=>({
        firstColumnText: state.tradeMarket.columns.first,
        secondColumnText: state.tradeMarket.columns.second,
        thirdColumnText: state.tradeMarket.columns.third
    })
)(TradeMarks)


